.projects
{   display:flex;
    flex-direction: column;
    margin-top:0%;
    margin-bottom: 0%;   
    
    background-color: rgba(1, 1, 1, 0.7);
}


::-webkit-scrollbar {
    width: 1vw;
  }
  

  ::-webkit-scrollbar-track {
    background: rgba(1, 1, 1, 0.7);
  }
  
 
  ::-webkit-scrollbar-thumb {
    background: rgb(238, 238, 238);
  }
  

  ::-webkit-scrollbar-thumb:hover {
background: rgb(214, 214, 214);

  }

iframe{
 
    margin:auto;
    margin-top:0;
    align-self: center;
    display: block;
    width:50vw;
    height: 30vw;
}
img{
    width:50vw;
    height: 30vw;
    margin:auto;
}
h1{
    margin-top: 10vh;
    font-size: 8vh;
    color:white;
  }

h2{
    margin-top: 0;
    margin-bottom: 0;
    color:white;
    font-size: 6vh;
    font-style: italic;
}

a{
    color:white;
}
a:hover{
    color:gainsboro;
}